
.App:nth-child(2){
  display: flex;
  width: 100%;
  height: 100%;
  align-items: center;
  justify-content: center;
}
.payment_form {
  position: relative;
  width: 80%;
  box-shadow: 0px 0px 0px 0.5px rgba(50, 50, 93, 0.1),
    0px 2px 5px 0px rgba(50, 50, 93, 0.1), 0px 1px 1.5px 0px rgba(0, 0, 0, 0.07);
  border-radius: 7px;
  padding: 40px;
  background-color: rgb(195, 195, 195);
  z-index: 4;
  animation: payment_form_ani 0.3s 1;
}


@keyframes payment_form_ani {
  0%{
    transform: translateX(100vw);
  }
}

  
#payment-message {
  color: rgb(105, 115, 134);
  font-size: 16px;
  line-height: 20px;
  padding-top: 12px;
  text-align: center;
  z-index: 4;
}
  
#payment-element {
  margin-bottom: 24px;
  z-index: 4;
}

#submit {
  background: #a1761f;;
  font-family: Arial, sans-serif;
  color: #ffffff;
  border-radius: 4px;
  border: 0;
  padding: 12px 16px;
  font-size: 16px;
  font-weight: 600;
  cursor: pointer;
  display: block;
  transition: all 0.2s ease;
  box-shadow: 0px 4px 5.5px 0px rgba(0, 0, 0, 0.07);
  width: 100%;
  z-index: 4;
}
  
#submit:hover {
  filter: contrast(115%);
}
  
#submit:disabled {
  opacity: 0.5;
  cursor: default;
}
  
  

/* spinner/processing state, errors */
  
.spinner,
.spinner:before,
.spinner:after {
  border-radius: 50%;
  z-index: 4;
}
  
.spinner {
  color: rgb(218, 218, 218);
  font-size: 22px;
  text-indent: -99999px;
  margin: 0px auto;
  position: relative;
  width: 20px;
  height: 20px;
  box-shadow: inset 0 0 0 2px;
  -webkit-transform: translateZ(0);
  -ms-transform: translateZ(0);
  transform: translateZ(0);
}
  
.spinner:before,
.spinner:after {
  position: absolute;
  content: '';
  z-index: 4;
}
  
.spinner:before {
  width: 10.4px;
  height: 20.4px;
  background: #5469d4;
  border-radius: 20.4px 0 0 20.4px;
  top: -0.2px;
  left: -0.2px;
  -webkit-transform-origin: 10.4px 10.2px;
  transform-origin: 10.4px 10.2px;
  -webkit-animation: loading 2s infinite ease 1.5s;
  animation: loading 2s infinite ease 1.5s;
}
  
.spinner:after {
  width: 10.4px;
  height: 10.2px;
  background: #5469d4;
  border-radius: 0 10.2px 10.2px 0;
  top: -0.1px;
  left: 10.2px;
  -webkit-transform-origin: 0px 10.2px;
  transform-origin: 0px 10.2px;
  -webkit-animation: loading 2s infinite ease;
  animation: loading 2s infinite ease;
}
  
@keyframes loading {
  0% {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
  }
  
  100% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}
  
@media only screen and (max-width: 600px) {
  .payment_form {
    width: 100%;
    height: 60%;
  }
}




